import "./styles.css";
import menu from "../src/nnn";

function menus(index) {
  const items = [];
  const keys = Object.keys(menu[index]);
  const values = Object.values(menu[index]);
  for (let i = 0; i < values.length; i++) {
    items.push(
      <li>
        {keys[i]}:{values[i]}
      </li>
    );
  }
  return <ul>{items}</ul>;
}

function all_menus() {
  const items = [];
  for (let i = 0; i < menu.length; i++) {
    items.push(menus(i));
  }
  return <>{items}</>;
}
export default function App() {
  return (
    <>
      <h1>旅行代金</h1>
      {all_menus()}
    </>
  );
}
